import window from 'global/window';

export default function scrollToTopOfElement(selector) {
  const element = document.querySelector(selector);
  const scrollTop = element.getBoundingClientRect().top;
  window.scrollBy(0, scrollTop);
}

/**
 * Scroll to Element
 * @param { Element | Number } element
 * @param { Number } offset
 * @param { Number } duration
 */
export function scrollToElement({ element, offset = 0, duration = 100 }) {
  let elementOffsetTop = 0;

  if (element instanceof window.HTMLElement) {
    elementOffsetTop = element.getBoundingClientRect().top;
  }

  if (element instanceof Number) {
    elementOffsetTop = element;
  }

  const windowOffsetTop = window.scrollY || window.pageYOffset;
  const change = elementOffsetTop + offset;
  const increment = 20;

  const animateScroll = (elapsedTime) => {
    const newElapsedTime = elapsedTime + increment;
    const position = easeInOut(newElapsedTime, windowOffsetTop, change, duration);
    window.scrollTo(0, position);
    if (newElapsedTime < duration) {
      setTimeout(() => {
        animateScroll(newElapsedTime);
      }, increment);
    }
  };

  animateScroll(0);
}

export function easeInOut(currentTime, start, change, duration) {
  let newCurrentTime = currentTime / (duration / 2);

  if (newCurrentTime < 1) {
    return (((change / 2) * newCurrentTime) * newCurrentTime) + start;
  }
  newCurrentTime -= 1;
  return ((-change / 2) * ((newCurrentTime * (newCurrentTime - 2)) - 1)) + start;
}



